/* Page Header */
.wp-block-g263-plus-page-header {
    --tw-bg-opacity: 1;
    background-color: rgb(243 244 246 / var(--tw-bg-opacity));
    padding-top: 2rem;
    padding-bottom: 2rem;
    margin-top: 0 !important;
  }
  .wp-block-g263-plus-page-header .inner-page-header {
    width: 100%
  }
  @media (min-width: 640px) {
    .wp-block-g263-plus-page-header .inner-page-header {
      max-width: 640px
    }
  }
  @media (min-width: 768px) {
    .wp-block-g263-plus-page-header .inner-page-header {
      max-width: 768px
    }
  }
  @media (min-width: 1024px) {
    .wp-block-g263-plus-page-header .inner-page-header {
      max-width: 1024px
    }
  }
  @media (min-width: 1280px) {
    .wp-block-g263-plus-page-header .inner-page-header {
      max-width: 1280px
    }
  }
  @media (min-width: 1536px) {
    .wp-block-g263-plus-page-header .inner-page-header {
      max-width: 1536px
    }
  }
  .wp-block-g263-plus-page-header .inner-page-header {
    margin-left: auto;
    margin-right: auto
  }
  .wp-block-g263-plus-page-header h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
    font-weight: 500;
    padding: 0;
  }